export default {
  isLoading: ({ loading }) => type => loading.includes(type),
  getLeagueTable: ({ league }) => {
    const teamsMap = new Map();

    league.teams.forEach((team) => {
      teamsMap.set(team.name, team);
    });

    league.table.standing.forEach((standing) => {
      standing.team = teamsMap.get(standing.teamName);
    });
    
    return league.table;
  },
  getLeagueTeams: ({ league }) => league.teams,
  getLeagueFixtures: ({ league }) => league.fixtures,
  getLeagueFixturesSorted: ({ league }) => direction => league.fixtures,
  getLeagueFixturesByStatus: state => (status = 'ALL') => (status === 'ALL') ? state.league.fixtures : state.league.fixtures.filter(fixture => fixture.status === status.toUpperCase()),
  getLeagues: ({ leagues }) => leagues,
};



