<template>
<div>
  <div class="card" v-for="competition in competitions" :key="competition.id">
    <div class="card-content">
      <div class="media">
        <div class="media-content">
          <router-link :to="{ name: 'LeagueTable', params: { 'competition': competition.id }}">
            <p class="title is-4" v-text="competition.caption"></p>
          </router-link>
          <p class="subtitle is-6">
            Number of teams: <span v-text="competition.numberOfTeams"></span>
          </p>
        </div>
      </div>
      <div class="has-text-right">
        <small>
          Updated
          <time :title="competition.lastUpdated" :datetime="competition.lastUpdated">
            {{ competition.lastUpdated | fromNow }}
          </time>
        </small>
      </div>
      <div class="content">
      </div>
    </div>
  </div>
  <br>
</div>
</template>

<script>
import { mapGetters } from 'vuex';
import DateParsers from '@/mixins/DateParsers';

export default {
  name: 'CompetitionsTable',
  mixins: [DateParsers],
  computed: {
    ...mapGetters({
      competitions: 'getLeagues',
    }),
  },
};
</script>



