<template>
  <div>
    <div v-if="loading">Loading...</div>
    <div v-else>
      <div class="field is-pulled-right">
      <div class="control">
        <div class="select">
          <select class="select" v-model="matchDay" @change="updateTable">
            <option value="">Last match day</option>
            <option v-for="day in matchDays" :key="day" v-text="day" :value="day"></option>
          </select>
        </div>
      </div>
    </div>
      <table class="table is-hoverable is-fullwidth">
        <thead>
          <tr>
            <th><abbr title="Position">Pos</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Crest">Crest</abbr></th>
            <th>Team</th>
            <th><abbr title="Played">Pld</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Won">W</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Drawn">D</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Lost">L</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Goals for">GF</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Goals against">GA</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Goal difference">GD</abbr></th>
            <th><abbr title="Points">Pts</abbr></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="standing in leagueTable.standing" :key="standing.teamName">
            <th v-text="standing.position"></th>
            <td class="is-hidden-mobile">
              <figure class="image is-square">
                <img :src="standing.team.crestUrl">
              </figure>
            </td>
            <td>
              <router-link :to="{ name: 'Team', params: { id: standing.team.id }}">
                {{ standing.team.name }}
              </router-link>
            </td>
            <td v-text="standing.playedGames"></td>
            <td class="is-hidden-mobile" v-text="standing.wins"></td>
            <td class="is-hidden-mobile" v-text="standing.draws"></td>
            <td class="is-hidden-mobile" v-text="standing.losses"></td>
            <td class="is-hidden-mobile" v-text="standing.goals"></td>
            <td class="is-hidden-mobile" v-text="standing.goalsAgainst"></td>
            <td class="is-hidden-mobile" v-text="standing.goalDifference"></td>
            <td v-text="standing.points"></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'LeagueTable',
  props: {
    competition: {
      required: true,
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => vm.fetchLeagueTable());
  },
  watch: {
    $route: 'fetchLeagueTable',
  },
  data() {
    return {
      matchDay: '',
      matchDays: 48,
    };
  },
  computed: {
    loading() {
      return this.$store.getters.isLoading('leagueTable');
    },
    ...mapGetters({
      league: 'getLeagueTable',
      leagueTable: 'getLeagueTable',
    }),
  },
  methods: {
    updateTable() {
      this.fetchLeagueTable();
    },
    fetchLeagueTable() {
      this.$store.dispatch('fetchLeague', { id: this.competition, params: { matchday: this.matchDay } });
    },
  },
};
</script>



