<template>
    <nav class="navbar is-transparent is-info is-fixed-top">
        <div class="navbar-brand">
            <router-link :to="{ name: 'Home' }" class="navbar-item">
                <img
                    height="32"
                    width="32"
                    src="../assets/logo.png"
                >
                {{ appName }}
            </router-link>
            <div class="navbar-burger burger" data-target="navbarExampleTransparentExample">
                <span></span>
                <span></span>
                <span></span>
            </div>
        </div>

        <div id="navbarExampleTransparentExample" class="navbar-menu">
            <div class="navbar-start">
                <div class="navbar-item" v-if="loading">Loading...</div>
                <div class="navbar-item has-dropdown is-hoverable" v-else>
                    <router-link
                        :to="{ name: 'CompetitionsTable' }"
                        class="navbar-link">
                        {{ competitions.length }} Leagues
                    </router-link>
                    <div class="navbar-dropdown is-boxed">
                        <router-link
                            v-for="competition in competitions"
                            :key="competition.id"
                            :to="{ name: 'LeagueTable', params: { 'competition': competition.id } }"
                            class="navbar-item">
                            {{ competition.caption }}
                        </router-link>
                    </div>
                </div>
            </div>

            <div class="navbar-end">
            <div class="navbar-item">
                <div class="field is-grouped">
                <p class="control">
                    <a class="button is-success" href="https://www.football-data.org/" target="_blank">
                        <span class="icon"><i class="fas fa-link"></i></span>
                        <span>football-data</span>
                    </a>
                </p>
                </div>
            </div>
            </div>
        </div>
    </nav>
</template>
<script>
import { mapGetters } from 'vuex';
import BulmaBurger from '@/helpers/BulmaBurger';
import { APP_NAME } from '@/config';

export default {
  name: 'Navbar',
  mounted() {
    BulmaBurger();
    this.$store.dispatch('fetchCompetitions', {});
  },
  computed: {
    appName() {
        return APP_NAME;
    },
    loading() {
      return this.$store.getters.isLoading('competitions');
    },
    ...mapGetters({
      competitions: 'getLeagues',
    }),
  },
};
</script>



