import { acceptedParams } from '@/config/api';

export default {
  paramsAreValid(uri, params) {
    // If no params are passed, good to go
    if (Object.keys(params).length === 0) {
      return true;
    }
    // If endpoint doesn't have any params
    if (!acceptedParams[uri]) {
      return true;
    }

    // Check if params passed are allowed in endpoint
    return Object.keys(params).some(param => acceptedParams[uri].includes(param));
  },
};



