<template>
  <div>
    <section class="hero is-info">
      <div class="hero-body">
        <div class="container">
          <h1 class="title is-spaced" v-text="league.leagueCaption"></h1>
          <h4 class="subtitle">
            <span class="image is-32x32" v-for="team in teams" :key="team.name">
              <img :src="team.crestUrl">
            </span>
          </h4>
        </div>
      </div>
    </section>
    <div class="tabs">
      <ul>
        <router-link :to="{ name: 'LeagueTable', params: { 'competition': competition }}" tag="li">
          <a>Table</a>
        </router-link>
        <router-link :to="{ name: 'AllFixtures', params: { 'competition': competition }}" tag="li">
          <a>Fixtures</a>
        </router-link>
      </ul>
    </div>
    <div>
      <div class="container">
        <transition name="view">
          <router-view></router-view>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'League',
  props: {
    competition: {
      required: true,
    },
  },
  mounted() {
    if (!this.league.leagueCaption) {
      this.fetchLeague();
    }
  },
  computed: {
    ...mapGetters({
      league: 'getLeagueTable',
      teams: 'getLeagueTeams',
    }),
  },
  methods: {
    fetchLeague() {
      this.$store.dispatch('fetchLeague', { id: this.competition, params: { matchday: this.matchDay } });
    },
  },
};
</script>
<style scoped>
  .subtitle .image { display: inline-block; margin-right: 10px; }
</style>



