<template>
  <div>

      <div class="columns">
        <nav class="panel column is-2">
          <router-link :to="{ name: 'AllFixtures' }" class="panel-block">
            <span class="tag is-pulled-right" v-text="allFixtures"></span>
            <div> All</div>
          </router-link>
          <router-link :to="{ name: 'FinishedFixtures' }" class="panel-block">
            <span class="tag is-pulled-right" v-text="finishedFixtures"></span>
            <div> Finished</div>
          </router-link>
          <router-link :to="{ name: 'TimedFixtures' }" class="panel-block">
            <span class="tag is-pulled-right" v-text="timedFixtures"></span>
            <div> Timed</div>
          </router-link>
          <router-link :to="{ name: 'ScheduledFixtures' }" class="panel-block">
            <span class="tag is-pulled-right" v-text="scheduledFixtures"></span>
            <div> Scheduled</div>
          </router-link>
          <router-link :to="{ name: 'CancelledFixtures' }" class="panel-block">
            <span class="tag is-pulled-right" v-text="cancelledFixtures"></span>
            <div> Cancelled</div>
          </router-link>
        </nav>
        <transition name="view" mode="out-in">
          <div v-if="loading">Loading...</div>
          <router-view v-else></router-view>
        </transition>
      </div>
  </div>
</template>

<script>
export default {
  name: 'LeagueFixtures',
  props: {
    competition: {
      required: true,
      validator(competition) {
        return competition > 0;
      },
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => vm.fetchLeagueFixtures());
  },
  computed: {
    loading() {
      return this.$store.getters.isLoading('leagueFixtures');
    },
    allFixtures() {
      return this.$store.getters.getLeagueFixturesByStatus().length;
    },
    finishedFixtures() {
      return this.$store.getters.getLeagueFixturesByStatus('FINISHED').length;
    },
    timedFixtures() {
      return this.$store.getters.getLeagueFixturesByStatus('TIMED').length;
    },
    cancelledFixtures() {
      return this.$store.getters.getLeagueFixturesByStatus('CANCELLED').length;
    },
    scheduledFixtures() {
      return this.$store.getters.getLeagueFixturesByStatus('SCHEDULED').length;
    },
  },
  methods: {
    fetchLeagueFixtures() {
      this.$store.dispatch('fetchLeagueFixtures', { id: this.competition });
    },
  },
};
</script>



