import api from '@/api';
import * as types from './mutation-types';
import * as LOAD from './loading-types';

const startLoading = type => ({ type, status: true });
const stopLoading = type => ({ type, status: false });

export default {
  fetchCompetitions({ commit }, params) {
    commit(types.LOADING, startLoading(LOAD.COMPETITIONS));

    api.getLeagues(params)
      .then(({ data }) => {
        commit(types.COMPETITIONS, data);
        commit(types.LOADING, stopLoading(LOAD.COMPETITIONS));
      })
      .catch((error) => {
        commit(types.LOADING, stopLoading(LOAD.COMPETITIONS));
        throw (error);
      });
  },

  fetchLeague({ commit }, { id, params }) {
    commit(types.LEAGUE_TABLE, {}); // Clear state before fetching new

    commit(types.LOADING, startLoading(LOAD.LEAGUE_TABLE));

    this.dispatch('fetchLeagueTeams', ({ id, params }));

    api.getLeagueTable(id, params)
      .then(({ data }) => {
        commit(types.LEAGUE_TABLE, data);
        commit(types.LOADING, stopLoading(LOAD.LEAGUE_TABLE));
      })
      .catch((error) => {
        commit(types.LOADING, stopLoading(LOAD.LEAGUE_TABLE));
        throw (error);
      });
  },

  fetchLeagueTeams({ commit }, { id, params }) {
    commit(types.LEAGUE_TEAMS, {}); // Clear state before fetching new

    commit(types.LOADING, startLoading(LOAD.LEAGUE_TEAMS));

    api.getLeagueTeams(id, params)
      .then(({ data }) => {
        commit(types.LEAGUE_TEAMS, data);
        commit(types.LOADING, stopLoading(LOAD.LEAGUE_TEAMS));
      })
      .catch((error) => {
        commit(types.LOADING, stopLoading(LOAD.LEAGUE_TEAMS));
        throw (error);
      });
  },

  fetchLeagueFixtures({ commit }, { id, params }) {
    commit(types.LEAGUE_FIXTURES, {}); // Clear state before fetching new

    commit(types.LOADING, startLoading(LOAD.LEAGUE_FIXTURES));

    api.getLeagueFixtures(id, params)
      .then(({ data }) => {
        commit(types.LEAGUE_FIXTURES, data);
        commit(types.LOADING, stopLoading(LOAD.LEAGUE_FIXTURES));
      })
      .catch((error) => {
        commit(types.LOADING, stopLoading(LOAD.LEAGUE_FIXTURES));
        throw (error);
      });
  },
};



