import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';

import Competitions from '@/components/Competitions';
import CompetitionsTable from '@/components/CompetitionsTable';

import League from '@/components/League';
import Fixtures from '@/components/Fixtures';
import LeagueTable from '@/components/LeagueTable';
import LeagueFixtures from '@/components/LeagueFixtures';

import Team from '@/components/Team';

Vue.use(Router);

export default new Router({
  linkActiveClass: 'is-active',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/competitions',
      component: Competitions,
      children: [
        {
          path: '',
          name: 'CompetitionsTable',
          component: CompetitionsTable,
        },
        {
          path: ':competition',
          name: 'League',
          component: League,
          props: true,
          children: [
            {
              path: 'table',
              name: 'LeagueTable',
              component: LeagueTable,
              props: true,
            },
            {
              path: 'fixtures',
              component: LeagueFixtures,
              name: 'LeagueFixtures',
              props: true,
              children: [
                {
                  path: 'all',
                  name: 'AllFixtures',
                  component: Fixtures,
                },
                {
                  path: 'finished',
                  name: 'FinishedFixtures',
                  component: Fixtures,
                  props: {
                    status: 'FINISHED',
                  },
                },
                {
                  path: 'timed',
                  name: 'TimedFixtures',
                  component: Fixtures,
                  props: {
                    status: 'TIMED',
                  },
                },
                {
                  path: 'scheduled',
                  name: 'ScheduledFixtures',
                  component: Fixtures,
                  props: {
                    status: 'SCHEDULED',
                  },
                },
                {
                  path: 'cancelled',
                  name: 'CancelledFixtures',
                  component: Fixtures,
                  props: {
                    status: 'CANCELLED',
                  },
                },
              ],
            },
          ],
        },
        {
          path: 'team/:id',
          name: 'Team',
          component: Team,
          props: true,
        },
      ],
    },
  ],
});



