import axios from 'axios';

const API_ENDPOINT = 'http://api.football-data.org';
// ATM the version is v1 (https://www.football-data.org/documentation)
const API_VERSION = 'v1';
// Get an API key @ https://www.football-data.org/client/register
const API_KEY = 'e42cb6a6ecc949c8897e06d284a55e05';
// export const API_KEY = '0718b7e522ed4ae8941b43e29b788c42'



// Default API configurations
axios.defaults.baseURL = `${API_ENDPOINT}/${API_VERSION}/`;
axios.defaults.headers['X-Auth-Token'] = API_KEY;

// Check if API key is setted before send request
axios.interceptors.request.use(
  (response) => {
    if (!response.headers['X-Auth-Token'] || !response.headers['X-Auth-Token'].length) {
      return Promise.reject(':: API key must be defined.');
    }

    return response;
  },
  error => Promise.reject(error),
);

export const acceptedParams = {
  'competitions': ['season'],
  'competitions/competition/leagueTable': ['matchday'],
  'competitions/competition/fixtures': ['timeFrame','matchday'],
  'fixtures': ['timeFrame', 'league'],
  'fixtures/fixture': ['head2head'],
  'teams/team/fixtures': ['season', 'timeFrame', 'venue'],
};

export default axios;


