import endpoint, { acceptedParams } from '@/config/api';
import helpers from './helpers';

export default {

  /**
   * Return a specific fixture
   *
   * @param Number fixture
   * @param {*} params
   * @returns Promise
   */
  getFixture(fixture, params = {}) {
    
    const uri = 'fixture/fixture';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {};
    return endpoint.get(`fixtures/${fixture}`, { params: Object.assign(defaults, params) });
  },

  /**
   * Returns all fixtures from all competitions
   *
   * @param {*} params
   * @returns Promise
   */
  getFixtures(params = {}) {

    const uri = 'fixtures';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {};
    return endpoint.get('fixtures', { params: Object.assign(defaults, params) });
  },

  /**
   * Returns all competitions
   *
   * @param {*} params
   * @returns Promise
   */
  getLeagues(params = {}) {
    
    const uri = 'competitions';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {
      season: new Date().getFullYear() - 1,
    };
    return endpoint.get('competitions', { params: Object.assign(defaults, params) });
  },

  /**
   * Return a specific competition's table
   *
   * @param Number competition
   * @param {*} params
   * @returns Promise
   */
  getLeagueTable(competition, params = {}) {

    const uri = 'competitions/competition/leagueTable';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {};
    return endpoint.get(`competitions/${competition}/leagueTable`, { params: Object.assign(defaults, params) });
  },

  /**
   * Return a specific competition's teams
   *
   * @param Number competition
   * @param {*} params
   * @returns Promise
   */
  getLeagueTeams(competition, params = {}) {
    
    const uri = 'competitions/competition/teams';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {};
    return endpoint.get(`competitions/${competition}/teams`, { params: Object.assign(defaults, params) });
  },

  /**
   * Return a specific competition's fixtures
   *
   * @param Number competition
   * @param {*} params
   * @returns Promise
   */
  getLeagueFixtures(competition, params = {}) {
    
    const uri = 'competitions/competition/fixtures';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {};
    return endpoint.get(`competitions/${competition}/fixtures`, { params: Object.assign(defaults, params) });
  },

  /**
   * Return a specific team
   *
   * @param Number team
   * @param {*} params
   * @returns Promise
   */
  getTeam(team, params = {}) {
    
    const uri = 'teams/team';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {};
    return endpoint.get(`teams/${team}`, { params: Object.assign(defaults, params) });
  },

  /**
   * Return a specific team's players
   *
   * @param Number team
   * @param {*} params
   * @returns Promise
   */
  getTeamPlayers(team, params = {}) {
    
    const uri = 'teams/team/players';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {};
    return endpoint.get(`teams/${team}/players`, { params: Object.assign(defaults, params) });
  },

  /**
   * Return a specific team's fixtures
   *
   * @param Number team
   * @param {*} params
   * @returns Promise
   */
  getTeamFixtures(team, params = {}) {
    
    const uri = 'teams/team/fixtures';
    if (! helpers.paramsAreValid(uri, params)) {
      return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
    }

    const defaults = {};
    return endpoint.get(`teams/${team}/fixtures`, { params: Object.assign(defaults, params) });
  },
};



